import React, { Fragment, useState } from 'react';
import axios from '../../../../../api';

import './index.css';

const PanelMainSection = () => {
  const [file, setFile] = useState('');
  const [filename, setFilename] = useState('Choose File');
  const [text, setText] = useState('');
  const [message, setMessage] = useState('');

  const onChange = e => {
    setFile(e.target.files[0]);
    setFilename(e.target.files[0].name);
  };
  const onTextChange = e => {
    setText(e.target.value);
  };

  const onSubmit = async e => {
    e.preventDefault();
    const formData = new FormData();
    formData.append('file', file);
    formData.append('text', text);

    console.log(file)
    console.log(text)

    try {
      const res = await axios.post('/upload', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        },
      });

      setMessage('File Uploaded');
    } catch (err) {
      if (err.response.status === 500) {
        setMessage('There was a problem with the server', err);
      } else {
        setMessage(err.response.data.msg);
      }
    }
  };

  return (
    <Fragment>
      {message ? <div>{message}</div> : null}
      <form className="main-section-form" name="form" onSubmit={onSubmit}>
        <div>
          <input type='file' onChange={onChange} />
          <label htmlFor='customFile'>{filename}</label> 
        </div>
        <div>
          <input type='text' value={text} onChange={onTextChange} />
        </div>
        

        <button type='submit' value='Upload'>Submit</button>
      </form>
    </Fragment>
  );
};

export default PanelMainSection;